<template>
  <b-overlay
    rounded="sm"
    :opacity="1"
    v-if="propertyDetails && propertyDetails.property"
  >
    <b-container class="container pt-4 unit-page-main">
      <!-- ___________________1____________________ -->
      <section class='card slider-card'>
        <div class="col-12 d-flex align-items-center justify-content-between">
          <div>
            <b-button
              class="back-btn d-none d-lg-block mb-3"
              v-b-tooltip.hover
              :title="$t('common.back')"
              variant="text"
              :to="localePath(`/search`)">
              <svg-icon
                fill="#204ECF"
                name="back"
                class="arrow-custom"
              ></svg-icon>
              <strong class="bold text-primary">{{ $t('common.back') }}</strong>
            </b-button>
            <h1 class="col-12 px-0 unit-page-name mb-2 is-desktop-device">
              {{ propertyDetails.property.property_name }}
            </h1>
          </div>
          <b-button
            variant="link"
            class="side-btn favourite-button"
            @click="updateFavourite"
          >
            <svg-icon
              :class="['fav-icon-size', dir === 'rtl' ? 'ml-1' : 'mr-1']"
              name="grey-unchecked-heart"
              :fill="propertyDetails.isFav ? '#fe4310' : '#204EC'"
            />
            <span> {{ dir === 'rtl' ? 'أضف إلى المفضلة' : 'Add to favourites' }}</span>
          </b-button>
        </div>

        <div class="mb-2 is-desktop-device">
          <div class="col-auto d-flex">
            <StarRateBox
              :withStarRating="false"
              :rate="propertyDetails.property.neighborhood_rating_avg"
              :reviews-count="
                propertyDetails.property.neighborhood_rating_count
              "
              class="star-rate-box"
            />
            <strong class="location-name">{{ Proplocation }}</strong>
          </div>
        </div>

        <div class="col-12 slider-wrapper">
          <!-- slider -->
          <CardImageSlider
            :images="propertyDetails.property.images"
            :ImageWidth="'320px'"
            :Imageheight="'400px'"
          />
        </div>
      </section>
      <section>
        <b-row class="mt-4">
          <b-col md="8" :class="{fakeHeight1: false}">
            <b-container>
              <!-- ___________________2____________________ -->
              <section>
                <div class="d-flex justify-content-between">
                  <div class="d-flex flex-column pt-2">
                    <div class='main-content-mobile  is-mobile-device'>

                      <div>
                        <img
                          v-if="user"
                          :src="`${user.avatar}`"
                          alt=""
                          class="profileImage"
                        />
                      </div>
                      <div>
                        <small class='is-desktop-device'>{{ $t('property.Entire_villa_hosted_by') }}</small>
                        <strong class='host-name' v-if="user">{{ user.full_name }}</strong>
                      </div>

                      <div class="mb-2">
                        <div class="col-auto d-flex">
                          <StarRateBox
                            :withStarRating="false"
                            :rate="propertyDetails.property.neighborhood_rating_avg"
                            :reviews-count="
                propertyDetails.property.neighborhood_rating_count
              "
                            class="star-rate-box"
                          />
                        </div>
                      </div>
                    </div>
                    <h1 class="col-12 px-0 unit-page-name mb-2 is-mobile-device">
                      {{ propertyDetails.property.property_name }}
                    </h1>
                    <strong class="location-name">{{ Proplocation }}</strong>
                    <div>
                      <PropertyDetails
                        :address="
                          propertyDetails.property.property_location_name
                        "
                        :number-of-bathroom="
                          propertyDetails.property.no_bathrooms
                        "
                        :number-of-bedroom="
                          propertyDetails.property.no_of_bedroom
                        "
                        :area="propertyDetails.property.property_area"
                      />
                    </div>
                  </div>
                  <div class="d-flex justify-content-center align-items-center is-desktop-device">
                    <img
                      v-if="user"
                      :src="`${user.avatar}`"
                      alt=""
                      class="profileImage"
                    />
                  </div>
                </div>
              </section>
              <hr />
              <!-- ___________________3____________________ -->
              <section>
                <div>
                  <h3 class='is-desktop-device'>
                    {{ $t('property.property_descriptionTitle1') }}
                  </h3>
                  <p class="text-sm mt-2">
                    {{ propertyDetails.property.description }}
                  </p>
                </div>
              </section>
              <br />
              <!-- ___________________4____________________ -->
              <section class='card'>
                <div>
                  <h3>{{ $t('property.what_place_offers') }}</h3>
                  <div>
                    <PropertyAmenities
                      :amenities-categories="propertyDetails.property.amenities"
                    />
                  </div>
                </div>
              </section>
              <!-- ___________________5____________________ -->
              <section class='card'>
                <div>
                  <h3>{{ $t('property.Property_location1') }}</h3>

                  <PropertyNearBy :propertyDetails="propertyDetails" />
                </div>
              </section>
              <hr />

              <!-- ___________________7____________________ -->
              <section class="mb-4 pb-4 card">
                <div class="d-flex justify-content-between">
                  <div>
                    <StarRateBox
                      :withStarRating="false"
                      :rate="propertyDetails.property.neighborhood_rating_avg"
                      :reviews-count="
                        propertyDetails.property.neighborhood_rating_count
                      "
                      class="star-rate-box"
                    />
                  </div>
                  <a href='#'>
                    {{ $t('property.view_all') }}
                  </a>
                </div>
                <div class="d-flex justify-content-start p-3 flex-column mb-4">
                  <!-- <div
                    class="d-flex flex-column mt-2 pt-3 px-3"
                    v-for="i in [1, 2]"
                    :key="i"
                  >
                    <div class="d-flex flex-row">
                      <img
                        v-if="user"
                        :src="`${$config.IMAGES_BASE_URL}/${user.avatar}`"
                        alt=""
                        class="profileImage"
                      />
                      <strong class="mx-1 mt-1">John Duo</strong>
                    </div>
                    <p class="px-4 py-2">
                      This is a dummy text and will be replaced with real text
                      later
                    </p>
                    <div>
                      <StarRateBox
                        :withStarRating="false"
                        :rate="propertyDetails.property.neighborhood_rating_avg"
                        :reviews-count="
                          propertyDetails.property.neighborhood_rating_count
                        "
                        class="star-rate-box"
                      />
                    </div>
                  </div> -->
                  <p class="lead"> {{ dir === 'rtl' ? 'لا يوجد أراء حتى الان ' : 'There is no Reviews yet.' }} </p>
                </div>
              </section>
              <!-- <hr /> -->
              <!-- ___________________8____________________ -->
            </b-container>
          </b-col>
          <!-- ___________________PropertyBid____________________ -->
          <b-col md="4" sm="12" class=" ">
            <PropertyBid
              :offerPrice="propertyDetails.property.offer_price"
              :property-price="propertyDetails.property.monthly_price"
              :property-daily-price="propertyDetails.property.daily_price"
              :property-security-deposite="
                propertyDetails.property.security_deposite
              "
              :property-rate="propertyDetails.property.property_rating_avg"
              :property-reviews-count="
                propertyDetails.property.property_rating_count
              "
              :property-name="propertyDetails.property.property_name"
              :property-term="propertyDetails.term"
              :property-id="propertyDetails.property.property_id"
              @onClick="goToPropertyReviews('#property-reviews-section')"
            />
          </b-col>
        </b-row>
        <!-- <pre>{{ propertyDetails }}</pre> -->
        <PropertyBidDetails />
      </section>
    </b-container>
  </b-overlay>
</template>
<script>
import PropertyAmenities from '@/components/property-details/PropertyAmenities'
import PropertyDetails from '@/components/property-details/NewPropertyDetails'
import PropertyBid from '@/components/property-details/PropertyBid'
import StarRateBox from '@/components/shared/StarRateBox'
import Breadcrumbs from '@/components/shared/Breadcrumbs'
import PropertyBidDetails from '~/components/property-details/PropertyBidDetails'
import SwitchInput from '@/components/shared/SwitchInput.vue'
import { scrollToElement } from '@/helpers/common'
import PropertyNearBy from '@/components/property-details/PropertyNearBy'
import { createNamespacedHelpers } from 'vuex'
import CardImageSlider from '@/components/shared/CardImageSlider.vue'
const { mapState: mapUserState } = createNamespacedHelpers('UserModule')

export default {
  components: {
    PropertyAmenities,
    PropertyDetails,
    PropertyBid,
    StarRateBox,
    Breadcrumbs,
    PropertyBidDetails,
    SwitchInput,
    PropertyNearBy,
    CardImageSlider,
  },
  data() {
    return {
      screenWidth: 0,
      propertyDetails: null,
    }
  },
  computed: {
    ...mapUserState(['user']),
    Proplocation() {
      const { city, district, property_location } = this.propertyDetails.property;
      // let loc = `${this.propertyDetails.property.city} , ${this.propertyDetails.property.district}`;
      let loc = `${!!district ? `${city} , ${district}` : `${property_location}`}`;
      return loc
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
  },
  async mounted() {
    let content = {
      propertySlug: this.$route.params.id,
      term: this.$route.query.term == 1 ? 1 : 0,
    }
    const { Data } = await this.$axios.$get(`/getSingleProperty/${content.propertySlug}?term=${content.term}`)
    this.propertyDetails = Data;
    if (process.browser) {
      this.setScreenWidth()
      window.addEventListener('resize', this.setScreenWidth)
    }
  },
  methods: {
    updateFavourite() {
      let formdata = {
        prop_id: this.propertyDetails.property.property_id,
        term: this.propertyDetails.term,
        type: 'single-property',
      }
      this.updatePropertyFavourite(formdata)
    },
    updatePropertyFavourite(payload) {
      if (!this.user) {
        this.$store.dispatch(
          'AuthModule/showAuthModal',
          {
            callbackAction: {
              name: 'PropertyModule/updatePropertyFavourite',
              payload,
            },
          },
          {
            root: true,
          }
        )
        return
      } else {
        return this.$axios
          .$post('/user/updateFavourite', payload)
          .then(() => {
            this.$store.commit('updateIsFavourite', payload)
          })
          .catch((error) => {
            if (error.response && error.response.data) {
              let errorMessage = error.response.data.Data.error.message
              this.$bvToast.toast(`${errorMessage} `, {
                title: 'Error',
                autoHideDelay: 5000,
                variant: 'danger',
                appendToast: true,
              })
            }
          })
      }
    },
    setScreenWidth() {
      this.screenWidth = window.innerWidth
    },
    goToPropertyReviews(hash) {
      scrollToElement(hash)
    },
  },
  head() {
    return {
      title: `property in  ${this.$route.params.id}`,
    }
  },
}
</script>
<style lang="scss" scoped>
a{
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #0078FF;
}
.card{
  background: #FFFFFF;
  border-radius: 16px;
  padding: 25px;
  margin-bottom: 20px;
  @include media-breakpoint-down(md) {
    &.slider-card{
      padding: 0;
    }
  }
}
h3{
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  color: #262D3D;
  @include media-breakpoint-down(md) {
    font-size: 18px;
  }
}
.slider-wrapper{
  @include media-breakpoint-down(md) {
    padding: 0;
  }
}
p{
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #262D3D;
}
.property-container {
  .property-top-bar {
    padding: 20px 0;
    align-items: baseline;

    .icons {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
      color: $mainTextColor;
      font-size: 14px;

      .side-btn {
        margin-left: 24px;

        [dir='rtl'] & {
          padding-right: 24px;
          padding-left: 0;
          margin-left: 0;
        }
      }

      .share-dropdown {
        font-size: 14px;
        background: none;
        border: 0;
        color: $mainTextColor;
      }

      .favourite-button {
        font-size: 14px;
        color: $mainTextColor;
        margin-bottom: 0;
      }

      .compare-btn {
        width: 170px;
        height: 45px;
        background-color: white;
        border-radius: 5px;
        border: 1px solid $mainColor;
        color: $mainColor;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .compare-icon {
          width: 20px;
          height: 14px;
        }
      }
    }

    svg {
      width: 18px;
      height: 18px;
    }
  }
}

.actions-container {
  position: absolute;
  top: 15px;
  right: 30px;
  left: 30px;
  z-index: 9;

  .actions-subcontainer {
    align-items: center;
  }

  .action-button {
    width: 28px;
    padding: 1px;
    height: 28px;
    background-color: white;
    border-radius: 5px;
    border: none;
    color: $mainTextColor;
    display: flex;
    justify-content: center;
    align-items: center;

    .action-icon {
      width: 18px;
      height: 18px;
    }
  }
}

.owner-mobile-name {
  font-size: 14px;
  text-decoration: underline;
}

::v-deep() .breadcrumb {
  padding: 0;
}

.back-btn {
  background: none;
  border: none;
  padding: 0;
  @include media-breakpoint-down(md) {
    position: absolute;
    left: 5px;
    top: 5px;
    z-index: 2;
    .text-primary{
      display: none;
    }
  }
}

.arrow-custom {
  width: 30px !important;
  height: 30px !important;
  transform: rotate(180deg);

  [dir='rtl'] & {
    transform: rotate(360deg);
  }
}

/****************************************************************************************************/

.main-content {
  padding: 1rem 0rem;
  padding-inline: 5rem;

  @include media-breakpoint-up(xs) {
    padding-inline: 0rem;
    padding: 0rem 0rem 0rem 0rem;
  }

  @include media-breakpoint-up(md) {
    padding: 1rem 0rem;
    padding-inline: 5rem;
  }
}

.fakeHeight {
  height: 400px;
}

.fakeHeight1 {
  height: 1800px;
}

.star-rate-box {
  margin-bottom: 30px;
}

.review-container {
  justify-content: space-between;
}

.review-sub-container {
  @include media-breakpoint-up(md) {
    max-width: 45%;
  }
}

.d-flex {
  @include media-breakpoint-down(md) {
    flex-direction: column !important;
  }
}

.fav-icon-size {
  width: 30px;
  height: 30px;
  top: 5px;
}

.profileImage {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  align-content: center;
}

.unit-page-main {
  .star-rate-box.rate {
    margin: 0 !important;
    margin-right: 15px !important;
  }
  .favourite-button {
    display: flex;
    align-items: center;
    height: auto !important;

    @include media-breakpoint-down(md) {
      span{
        display: none;
      }
      position: absolute;
      bottom: -420px;
      right: 20px;
      z-index: 2;
      background-color: white;
      border-radius: 50%;
      padding: 5px;
      box-shadow: 0px 4px 8px rgba(39, 39, 39, 0.08);
      svg{
        margin: 0;
      }
    }
  }
  .back-btn {
    display: flex !important;
    align-items: center;
    width: 85px;
    color: #204ecf;
    .arrow-custom {
      width: 23px !important;
      height: 23px !important;
      transform: none !important;
    }
  }
  .unit-page-name {
    font-weight: 700;
    font-size: 32px;
    line-height: 40px;
    color: #262D3D;
  }
  .location-name {
    color: #0f256e !important;
    font-size: 17px;
  }
}
.host-name{
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #0078FF;
}
.main-content-mobile{

  @include media-breakpoint-down(md) {
    display: flex !important;
    flex-direction: row;
    gap: 5px;
  }
}
</style>
