<template>
  <div>
    <div class="location-tabs">
      <div>
        <b-tabs content-class="mt-3">
          <b-tab :title="$t('property.propertyDetails.map')" active>
            <div id="map">
              <GoogleMap
                class="map-contianer"
                :location="{
                  lat: parseFloat(
                    propertyDetails.property.property_location.lat
                  ),
                  lng: parseFloat(
                    propertyDetails.property.property_location.lon
                  ),
                }"
                :property-name="propertyDetails.property.property_name"
                :property-marker="propertyMarker"
              />
            </div>
          </b-tab>
          <b-tab
            :title="$t('property.propertyDetails.schools')"
            @click="searchPlacesOnMap('school')"
            ><div id="school">
              <GoogleMap
                ref="school"
                map-reference="school"
                class="map-contianer"
                :location="{
                  lat: parseFloat(
                    propertyDetails.property.property_location.lat
                  ),
                  lng: parseFloat(
                    propertyDetails.property.property_location.lon
                  ),
                }"
                :property-name="propertyDetails.property.property_name"
                :property-marker="propertyMarker"
              /></div
          ></b-tab>
          <b-tab
            :title="$t('property.propertyDetails.cafes')"
            @click="searchPlacesOnMap('cafe')"
            ><div id="cafe">
              <GoogleMap
                ref="cafe"
                map-reference="cafe"
                class="map-contianer"
                :location="{
                  lat: parseFloat(
                    propertyDetails.property.property_location.lat
                  ),
                  lng: parseFloat(
                    propertyDetails.property.property_location.lon
                  ),
                }"
                :property-name="propertyDetails.property.property_location_name"
                :property-marker="propertyMarker"
              /></div
          ></b-tab>
          <b-tab
            :title="$t('property.propertyDetails.hospitals')"
            @click="searchPlacesOnMap('hospital')"
            ><div id="hospital">
              <GoogleMap
                ref="hospital"
                map-reference="hospital"
                class="map-contianer"
                :location="{
                  lat: parseFloat(
                    propertyDetails.property.property_location.lat
                  ),
                  lng: parseFloat(
                    propertyDetails.property.property_location.lon
                  ),
                }"
                :property-name="propertyDetails.property.property_name"
                :property-marker="propertyMarker"
              /></div
          ></b-tab>
          <b-tab
            :title="$t('property.propertyDetails.hotels')"
            @click="searchPlacesOnMap('hotel')"
            ><div id="hotel">
              <GoogleMap
                ref="hotel"
                map-reference="hotel"
                class="map-contianer"
                :location="{
                  lat: parseFloat(
                    propertyDetails.property.property_location.lat
                  ),
                  lng: parseFloat(
                    propertyDetails.property.property_location.lon
                  ),
                }"
                :property-name="propertyDetails.property.property_name"
                :property-marker="propertyMarker"
              /></div
          ></b-tab>
        </b-tabs>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.location-tabs {
  margin-top: 20px;
  margin-bottom: 18px;
  ::v-deep() .nav-tabs {
    border: 0;
    padding: 0;
  }
  ::v-deep() .nav-link.active {
    border: solid 1px $mainColorLight;
    background-color: rgba($mainColorLight, 0.1);
    color: $mainColorLight;
  }
  ::v-deep() .nav-item a {
    width: auto;
    height: 34px;
    padding: 4px 17px;
    border-radius: 15px;
    border: solid 1px $grayLightColor;
    margin-right: 12px;
    text-align: center;
    margin-top: 10px;
    [dir='rtl'] & {
      margin-left: 12px;
      margin-right: 0;
    }
  }
  .map-contianer {
    width: 100%;
    height: 340px;
  }
}
</style>
<script>
import GoogleMap from '@/components/shared/GoogleMap'

export default {
  name: 'PropertyNearBy',
  props: ['propertyDetails'],
  components: {
    GoogleMap,
  },
  data() {
    return {
      propertyMarker: require('@/assets/images/map/property-marker.png'),
    }
  },
  methods: {
    searchPlacesOnMap(place) {
      this.$refs[place].initGoogleNearBy(place)
    },
  },
}
</script>
