<template>
  <div class="d-flex align-items-center">
    <div @click="changeValue" class="d-flex align-items-center">
      <input type="checkbox" :checked="value" />
      <span class="switch-ui"></span>
    </div>
    <small
      v-b-tooltip.hover.bottom="$t('property.vacantOccupiedTooltip')"
      class="switch-label"
      >{{ label }}
    </small>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      required: true,
    },
    label: {
      required: true,
      type: String,
    },
  },
  methods: {
    changeValue() {
      this.$emit('input', !this.value)
    },
  },
}
</script>

<style lang="scss" scoped>
input[type='checkbox'] {
  height: 0;
  width: 0;
  visibility: hidden;
}

.switch-ui {
  cursor: pointer;
  text-indent: -9999px;
  width: 50px;
  height: 25px;
  background: grey;
  display: inline-block;
  vertical-align: middle;
  border-radius: 100px;
  position: relative;
}

.switch-ui:after {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 90px;
  transition: 0.3s;
}

input:checked + .switch-ui {
  background: $mainColor;
}

input:checked + .switch-ui:after {
  left: calc(100% - 5px);
  transform: translateX(-100%);
}
.switch-label {
  margin: 0px 5px;
  font-size: 14px;
  font-weight: 400;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
</style>
