<template>
  <div>
    <div id="carousel-container">
      <b-carousel
        id="carousel-1"
        v-model="slide"
        :interval="4000"
        controls
        indicators
        background="#ababab"
        img-width="1024"
        img-height="480"
        style="text-shadow: 1px 1px 2px #333"
        @sliding-start="onSlideStart"
        @sliding-end="onSlideEnd">
        <b-carousel-slide v-for="(image, index) in images" :key="index" class="">
          <template #img>
            <div
              class="d-block img-fluid w-100"
              :style="{
              height: Imageheight,
              width: ImageWidth,
              objectFit: 'fill',
              backgroundImage: `url('${image}')`,
              backgroundSize: 'cover',
              backgroundPosition: 'center'
            }"
            ></div>
          </template>
        </b-carousel-slide>
      </b-carousel>
    </div>
    <div class='is-desktop-device'>
      <div class='images-container'>
        <div v-for="(image, index) in images" :key="index"
             class="d-block img-fluid"
             :style="{
              height: '112px',
              width: '200px',
              objectFit: 'fill',
              backgroundImage: `url('${image}')`,
              backgroundSize: 'contain',
              backgroundPosition: 'center',
              backgroundRepeat: 'no-repeat'
            }"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CardImageSlider',
  data() {
    return {
      slide: 0,
      sliding: null,
    };
  },
  props: {
    images: {
      type: Array,
      default: [],
    },
    ImageWidth: {
      type: String,
      default: '273px',
    },
    Imageheight: {
      type: String,
      default: '200px',
    },
  },
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
    },
    onSlideEnd(slide) {
      this.sliding = false;
    },
  },
};
</script>
<style lang="scss" scoped>
.images-container{
  margin-top: 55px;
  display: flex;
  justify-content: center;
  gap:31px;
}
#carousel-container{
  margin: 0 10%;
  border-radius: 12px;
  overflow: hidden;
  @include media-breakpoint-down(md) {
    margin: 0;
  }
}
::v-deep() .carousel-control-prev-icon ,::v-deep()  .carousel-control-next-icon{
  width: 54px !important;
  height: 54px !important;
  background-color: black;
  transform: rotate(180deg);
  background-position: calc( 50% - 2px);
  background-image: url('data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iOSIgaGVpZ2h0PSIxNiIgdmlld0JveD0iMCAwIDkgMTYiIGZpbGw9Im5vbmUiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+CjxwYXRoIGQ9Ik04IDE1TDEgOEw4IDEiIHN0cm9rZT0id2hpdGUiIHN0cm9rZS13aWR0aD0iMiIgc3Ryb2tlLWxpbmVjYXA9InJvdW5kIiBzdHJva2UtbGluZWpvaW49InJvdW5kIi8+Cjwvc3ZnPgo=') !important;
}
::v-deep()  .carousel-control-next{
   @include media-breakpoint-down(md) {
     right: 5px !important;
   }
 }
::v-deep() .carousel-control-prev{

  @include media-breakpoint-down(md) {
    left: 5px !important;
  }
}
