<template>
  <GmapMap
    :ref="mapReference"
    :center="location"
    :zoom="15"
    map-type-id="terrain"
  >
    <GmapMarker
      :position="location"
      :clickable="true"
      :draggable="false"
      :icon="propertyMarker"
      :title="propertyName"
      @click="center = location"
    />
  </GmapMap>
</template>
<script>
import { gmapApi } from 'vue2-google-maps'
export default {
  name: 'GoogleMap',
  props: {
    mapReference: {
      type: String,
      required: false,
      default: 'map',
    },
    location: {
      type: Object,
      required: true,
    },
    propertyName: {
      type: String,
      required: true,
    },
    propertyMarker: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      map: {},
    }
  },
  computed: {
    google: gmapApi,
  },
  methods: {
    initGoogleNearBy(places) {
      this.map = new this.google.maps.Map(this.$refs[places].$el, {
        center: {
          lat: this.$refs[places].center.lat,
          lng: this.$refs[places].center.lng,
        },
        zoom: 15,
      })

      const request = {
        location: this.map.getCenter(),
        radius: 10000,
        types: [places],
      }

      const service = new this.google.maps.places.PlacesService(this.map)
      service.nearbySearch(request, this.callback)
    },
    callback(results, status) {
      if (status === this.google.maps.places.PlacesServiceStatus.OK) {
        const bounds = new google.maps.LatLngBounds()
        const defaultPlace = {
          geometry: {
            location: this.location,
          },
          name: this.propertyName,
          icon: this.propertyMarker,
        }
        this.createMarker(defaultPlace)

        results.forEach((place) => {
          place.icon = require('@/assets/images/map/place-marker.png')
          const marker = this.createMarker(place)
          bounds.extend(marker.getPosition())
        })
        this.map.fitBounds(bounds)
      }
    },
    createMarker(place) {
      const icon = {
        url: place.icon,
        scaledSize: new this.google.maps.Size(30, 30),
      }
      const marker = new google.maps.Marker({
        position: place.geometry.location,
        map: this.map,
        title: place.name,
        icon,
      })

      return marker
    },
  },
}
</script>
